/*************** wrap ***************/
.wrap{
    width: 1300px;
    margin: 0 auto;
    margin-top: 160px;
    overflow: hidden;
    padding: 0 240px;
    box-sizing: border-box;
}
.wrap .log{
    width: 420px;
    height: 600px;
    margin-right: 50px;
    float: left;
}

.wrap .log h1{
    text-align: center;
}
/* 로그인 */
.log_id{
    position: relative;
    width: 410px;
    margin-top: 60px;
} 
.log_pw {
    position: relative;
    width: 410px;
    margin-top: 50px;
  }
  .log_id>div{
    position: absolute;
    top: 0; right: 0;
    scale: 1.2;
    color: #222;

  }
  
  .log_pw>div{
    position: absolute;
    top: 0; right: 0;
    color: #222;
  }
  
  .log_id input , .log_pw input{
    font-size: 15px;
    color: #222222;
    width: 410px;
    border: none;
    border-bottom: solid #aaaaaa 1px;
    padding-bottom: 10px;
    padding-left: 10px;
    position: relative;
    background: none;
    z-index: 5;
  }
  
  .log_id input::placeholder,
  .log_pw input::placeholder
   { color: #aaaaaa; }

  .log_id input:focus,
  .log_pw input:focus { outline: none; }
  
  .log_id .id_in,
  .log_pw .pw_in
   {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0%;  /* right로만 바꿔주면 오 - 왼 */
    background-color: #666;
    width: 0;
    height: 2px;
    border-radius: 2px;
    transition: 0.5s;
  }
  
  .log_id label,
  .log_pw label
   {
    position: absolute;
    color: #aaa;
    left: 10px;
    font-size: 16px;
    bottom: 8px;
    transition: all .2s;
  }
  
  .log_id input:focus ~ label, input:valid ~ label,
  .log_pw input:focus ~ label, input:valid ~ label
   {
    font-size: 14px;
    bottom: 40px;
    color: #666;
    font-weight: bold;
  }
  
  .log_id input:focus ~ .id_in, input:valid ~ .id_in,
  .log_pw input:focus ~ .pw_in, input:valid ~ .pw_in {
    width: 100%;
  }

  /*  */
.wrap .log .find_pw{
    height: 20px;
    margin: 30px 0 10px 0;
    width: 147px;
    cursor: pointer;
}
.wrap .log .find_pw:hover{
